import React from "react";

import "../styles/elementsMineS/Modal.scss";

const Modal = ({ showModal, setShowModal }) => {
  if (!showModal) return null;

  const handleAccept = () => {
    setShowModal(false);
  };

  return (
    <div className="modal-overlay">
      <div className="modal">
        <h2>Advertencia!</h2>
        <p>
            Puedes entrar en el bosque pero antes debes saber tres cosas:
            - Necesitas 3 tipos de objetos.
            - Debes guardar los objetos en tu bolsa.
            - Mucho cuidado con los ladrones, si encuentras una tendrs que volver a empezar y si adems no has guardado los objetos los perders.
        
        </p>
        <button onClick={handleAccept}>Aceptar</button>
      </div>
    </div>
  );
};

export default Modal;
